import back from '@/components/back'
export default {
	components:{
        back
    },
	data () {
		return {
			noticeId:this.$route.query.noticeId,
			info:'',
			PublishTime:'',
			LinkUrl:null
		}
	},
	created() {
		this.getNotice()
	},
	methods: {
		getNotice(){
			this.$get('PlatFormAPI/HealthInformation/GetNoticeManageById', {
				NoticeManageId:this.noticeId
			}).then(o => {
				this.info = o
				this.PublishTime = o.PublishTime.split(' ')[0]
				this.LinkUrl = o.LinkUrl
			})
		},
	}
}



